import React, { useState, useEffect } from "react";
import "./IngredientsDesktop.scss";
import * as ScrollMagic from "scrollmagic";
import gsap from "gsap";
import HamMushroomIngredients2 from "./images/szynka-pieczarki.png";
import HamMushroomIngredients3 from "./images/cebula-pieczarka.png";

const IngredientsDesktop = () => {
  // eslint-disable-next-line
  const [scrollMagic, setScrollMagic] = useState({
    controller: new ScrollMagic.Controller(),
    timelineOne: gsap.timeline(),
  });
  const { controller, timelineOne } = scrollMagic;
  useEffect(() => {
    // ingredients animation
    timelineOne.staggerFromTo(
      [".hamMushroomIngredients2", ".hamMushroomIngredients3"],
      0.5,
      { y: "50px", opacity: 0 },
      { y: "0", opacity: 1 },
      0.25
    );

    new ScrollMagic.Scene({
      triggerElement: ".ingredients_desktop2",
      triggerHook: 0.7,
    })
      .setTween(timelineOne)
      .addTo(controller);
    // eslint-disable-next-line
  }, []);
  return (
    <div className="ingredients_desktop2">
      <img
        src={HamMushroomIngredients2}
        alt="Szynka pieczarki"
        className="hamMushroomIngredients2"
      />
      <img
        src={HamMushroomIngredients3}
        alt="Cebula pieczarka"
        className="hamMushroomIngredients3"
      />
    </div>
  );
};

export default IngredientsDesktop;
